/* Estilos para el Portafolio Detallado */
.portafolio {
  position: relative;
  padding: 60px 20px; /* Añadido padding para más espacio arriba y abajo */
  background-color: #020817; /* Color del fondo del contenedor */
  border-radius: 15px; /* Bordes redondeados */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* Sombra para resaltar el contenedor */
  max-width: 1200px; /* Ajusta el ancho máximo según el diseño */
  margin-left: auto;
  margin-right: auto;
  display: flex;
  flex-direction: column;
  justify-content: flex-start; /* Alinea desde la parte superior */
  min-height: 100vh; /* Altura mínima de la pantalla */
  overflow: hidden; /* Evita que el contenido se desborde */
  }
  
  .portafolio h2 {
    font-size: 2.5rem;
    font-weight: bold;
    color: var(--color-texto);
    margin-bottom: 10px;
    text-transform: uppercase;
    margin-bottom: 60px;
  }
  .portafolio-detallado h1 {
    text-align: center;
    color: #ffffff !important;
    margin-bottom: 30px;
  }
  
  .proyecto-detalle {
    background-color: white;
    border-radius: 10px;
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
    padding: 25px;
    margin-bottom: 30px;
    transition: all 0.3s ease;
  }
  
  .proyecto-detalle:hover {
    transform: translateY(-5px);
    box-shadow: 0 6px 20px rgba(0, 0, 0, 0.15);
  }
  
  .proyecto-detalle h2 {
    color: #2c3e50;
    margin-bottom: 15px;
  }
  
  .proyecto-detalle p {
    color: #555;
    margin-bottom: 20px;
    line-height: 1.6;
  }
  
  .proyecto-detalle h3 {
    color: #2c3e50;
    margin-top: 20px;
    margin-bottom: 10px;
  }
  
  .proyecto-detalle ul {
    padding-left: 20px;
    color: #555;
  }
  
  .proyecto-detalle li {
    margin-bottom: 5px;
  }
  
  /* Estilos para proyectos destacados */
  .proyecto-detalle.destacado {
    border: 2px solid #3498db;
    position: relative;
    overflow: hidden;
  }
  
  .proyecto-detalle.destacado::before {
    content: '🌟 Destacado';
    position: absolute;
    top: 10px;
    right: -30px;
    background: #3498db;
    color: white;
    padding: 5px 30px;
    transform: rotate(45deg);
    font-size: 0.8em;
  }
  
  /* Estilos para proyectos de IA */
  .proyecto-detalle.ia {
    background: linear-gradient(145deg, #ffffff, #e6f7ff);
  }
  
  .proyecto-detalle.ia h2 {
    color: #0056b3;
  }
  
  /* Animación para fade in */
  @keyframes fadeInUp {
    from {
      opacity: 0;
      transform: translateY(20px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }
  
  .proyecto-detalle {
    animation: fadeInUp 0.5s ease-out;
  }
  
  /* Estilo para el enlace de los proyectos */
.proyecto-link {
  display: inline-block;
  margin-top: 15px;
  padding: 10px 20px;
  background-color: #3498db;
  color: #fff;
  text-decoration: none;
  border-radius: 5px;
  transition: background-color 0.3s ease;
  font-weight: bold;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}

.proyecto-link:hover {
  background-color: #2980b9;
  box-shadow: 0 6px 15px rgba(0, 0, 0, 0.2);
  transform: translateY(-3px);
}

/* Contenedor principal del proyecto */
.proyecto-card {
  position: relative; /* Necesario para que el botón se posicione correctamente */
  padding-bottom: 60px; /* Espacio suficiente para el botón en la parte inferior */
}

/* Contenedor del botón de llamada a la acción */
.proyecto-cta {
  position: absolute;
  bottom: 20px; /* Coloca el botón en la parte inferior */
  right: 20px; /* Coloca el botón en la esquina derecha */
}

/* Estilo para el botón de llamada a la acción */
.cta-button {
  padding: 8px 15px;
  font-size: 0.9rem;
  background-color: #00cc99; /* Color verde neón para el botón */
  color: white;
  border: none;
  border-radius: 25px;
  cursor: pointer;
  transition: all 0.3s ease;
  display: inline-flex;
  align-items: center;
  gap: 5px;
  text-transform: uppercase;
  letter-spacing: 1px;
  box-shadow: 0 4px 6px rgba(0,0,0,0.2);
  text-decoration: none;
  font-weight: bold;
}

.cta-button:hover {
  background-color: #009966; /* Color verde más oscuro al pasar el ratón */
  transform: translateY(-2px);
  box-shadow: 0 6px 8px rgba(0,0,0,0.3);
}